import * as Yup from 'yup';
import PropTypes from 'prop-types';
import { useMemo, useEffect } from 'react';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// swal
import swal from 'sweetalert';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Alert, Stack } from '@mui/material';
import axios from '../../utils/axios';
import { FormProvider, RHFTextField } from '../../components/hook-form';

// drop dwon style
import useIsMountedRef from '../../hooks/useIsMountedRef';
// ----------------------------------------------------------------------
HabukhanResultCharge.propTypes = {
  discount: PropTypes.object,
};
export default function HabukhanResultCharge({ discount }) {
  const isMountedRef = useIsMountedRef();
  const NewUserSchema = Yup.object().shape({
    waec: Yup.string().required(),
    nabteb: Yup.string().required(),
    neco: Yup.string().required(),
  });

  const defaultValues = useMemo(
    () => ({
      waec: discount?.waec,
      nabteb: discount?.nabteb,
      neco: discount?.neco
    }),
    [discount]
  );
  const AccessToken = window.localStorage.getItem('accessToken');
  const methods = useForm({
    resolver: yupResolver(NewUserSchema),
  });
  useEffect(() => {
    if (discount !== undefined) {
      reset(defaultValues);
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [discount]);
  const {
    reset,
    setError,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = methods;

  const onSubmit = async (data) => {
    try {
      await axios.post(`/api/edit/result/charges/account/${AccessToken}/habukhan/secure`, data)
      swal('success', `Result Checker Charges Updated Successfully`, 'success');
    } catch (error) {
      if (isMountedRef.current) {
        setError('afterSubmit', error);
      }
    }
  };

  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>

        <Grid item xs={12} md={8}>
          {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
          <Card sx={{ p: 3 }}>

            <Box
              sx={{
                display: 'grid',
                columnGap: 3,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(1, 1fr)' },
              }}
            >
              <RHFTextField name="waec" label="WAEC" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="neco" label="NECO" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="nabteb" label="NABTEB" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
            </Box>
            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton fullWidth size="large" type="submit" variant="contained" loading={isSubmitting}>
                Proceed
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
